<template>
  <div>
    <transition name="dialog-fade">
      <div v-show="dialogShow" class="dialog-box">
        <div class="dialog-wrapper">
          <div class="dialog-head">{{headText}}</div>
          <div class="dialog-content">
            <slot></slot>
          </div>
          <div class="dialog-btns">
            <slot name="btn"></slot>
            <button class="btn-confirm" @click="confirmClick">{{confirmBtnText}}</button>
            <button v-if="type=='confirm'" @click="cancelClick">{{cancelBtnText}}</button>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
export default{
  name: 'Dialog',
  props: {
    // typeconfirmalert
    type: {
      type: String,
      default: 'confirm'
    },
    // 
    headText: {
      type: String,
      default: ''
    },
    // html
    bodyText: {
      type: String,
      default: ''
    },
    // 
    cancelBtnText: {
      type: String,
      default: ''
    },
    // 
    confirmBtnText: {
      type: String,
      default: ''
    },
    // Dialog  body 
    appendToBody: {
      type: Boolean,
      default: true
    }
  },
  data(){
    return{
      dialogShow: false
    }
  },
  methods:{
    show() {
      this.dialogShow = true
    },
    hide() {
      this.dialogShow = false
    },
    confirmClick() {
      this.$emit('confirm')
    },
    cancelClick() {
      this.hide()
      this.$emit('cancel')
    }
  }
}
</script>

<style lang="scss" scoped>
@import './index.scss'
</style>
